.arena-bar-team {
  /* background: ; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  /* border-left: 1px solid rgba(0, 0, 0, 0.1); */
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.arena-bar-team__rank {
  width: 80px;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  /* background: #1d181d;
  color: #fff; */
  font-size: 20px;
  font-weight: 800;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.arena-bar-team__rank.first {
  background: #fdc823;
}
.arena-bar-team__rank.second {
  background: #dcdee2;
}
.arena-bar-team__rank.third {
  background: #f0a969;
}

.arena-bar-team__rank__number {
  font-size: 26px;
}
.arena-bar-team__rank__ordinal {
  font-size: 14px;
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
}

.arena-bar-team__name {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0 10px;
  font-weight: 600;
  font-size: 17px;
  line-height: 1;
  overflow: hidden;
}

.arena-bar-team__name--inner {
  /* width: 100%; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  white-space: nowrap;
  max-width: 180px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  width: 100%;
}

.arena-bar-team__name--item {
  display: inline-block;
  /* vertical-align: bottom; */
}

.arena-bar-team__name--item:last-child {
  margin-top: 5px;
}

.arena-bar-team__name--seperator {
  display: inline-block;
  margin: 0 5px;
  opacity: 0.6;
}

.arena-bar-team__avatar {
  position: relative;
  width: 56px;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
}

.arena-bar-team__avatar--img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.arena-bar-team__score {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 68px;
  color: #000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 24px;
  font-weight: 800;
  position: relative;
}

.arena-bar-team__score.animate-score {
  -webkit-animation: score-bounce 0.4s ease forwards;
          animation: score-bounce 0.4s ease forwards;
}


@-webkit-keyframes score-bounce { 
  0%,100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
}


@keyframes score-bounce { 
  0%,100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
}

.arena-bar-team__score--diff {
  position: absolute;
  width:30px;
  height: 30px;
  top: 50%;
  left:50%;
  font-size: 24px;
  margin-left: -15px;
  margin-top: -15px;
  text-align: center;
  -webkit-animation: new-score-float 1s ease forwards;
          animation: new-score-float 1s ease forwards;
}

@-webkit-keyframes new-score-float {
  0% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
    opacity:1
  }
  60% {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
    opacity: 1
  }
  100% {
    -webkit-transform: translate3d(0, -200%, 0);
            transform: translate3d(0, -200%, 0);
    opacity: 0;
  }
}

@keyframes new-score-float {
  0% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
    opacity:1
  }
  60% {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
    opacity: 1
  }
  100% {
    -webkit-transform: translate3d(0, -200%, 0);
            transform: translate3d(0, -200%, 0);
    opacity: 0;
  }
}

.avatars-2 .arena-bar-team__avatar--img:last-child {
  -webkit-clip-path: polygon(100% 0, 0 100%, 100% 100%);
          clip-path: polygon(100% 0, 0 100%, 100% 100%);
}

.avatars-3 .arena-bar-team__avatar--img:nth-child(2) {
  -webkit-clip-path: polygon(50% 0, 0 100%, 100% 100%);
          clip-path: polygon(50% 0, 0 100%, 100% 100%);
}

.avatars-3 .arena-bar-team__avatar--img:nth-child(3) {
  -webkit-clip-path: polygon(50% 0, 0 100%, 0 0);
          clip-path: polygon(50% 0, 0 100%, 0 0);
}


.arena-bar__avatar-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  left:0;
  top:0;
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.arena-bar__avatar-overlay--icon {
  height: 50px;
  width: 50px;
  opacity: 0.8;
}


