.arena-bar-background {
  z-index: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  /* background: red; */
}

.arena-bar-background__img {
  position: absolute;
  left: 0;
  bottom: -20px;
}


.arena-bar-background.animate .arena-bar-background__img:nth-child(1) {
  -webkit-animation: fade-in-out 3s infinite;
          animation: fade-in-out 3s infinite;
}

.arena-bar-background.animate .arena-bar-background__img:nth-child(2) {
  -webkit-animation: fade-in-out 3s infinite;
          animation: fade-in-out 3s infinite;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}

.arena-bar-background.animate .arena-bar-background__img:nth-child(3) {
  -webkit-animation: fade-in-out 3s infinite;
          animation: fade-in-out 3s infinite;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}


@-webkit-keyframes fade-in-out {
  0% {
    opacity: 0;
  }
  500%{
    opacity:1;
  }
  100% {
    opacity: 0;
  }
}


@keyframes fade-in-out {
  0% {
    opacity: 0;
  }
  500%{
    opacity:1;
  }
  100% {
    opacity: 0;
  }
}






