.arena-bar-logo {
  width: 160px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  position: relative;
}

.arena-bar-logo__img {
  width: 140px;
  height: 140px;
  display: block;
  /* background: teal; */
  position: absolute;
  bottom: 10px;
  left: 10px;
  border: none;
  outline: none;
  -o-object-fit: contain;
     object-fit: contain;
  z-index: 99;
}



